import React, { Component } from 'react';
import './shipment.css';
import PropTypes from 'prop-types';

export default class Shipment extends Component {

    static propTypes = {
        total: PropTypes.number
    }
    
    render() {

        const { total } = this.props;

        const shipping = total > 0 && total < 500 ? 350 : 99;

        const shippingNeon =
         shipping === 99 ? (<span className="red">{shipping} ₽</span>) : (<span>{shipping} ₽</span>);

        return (
            <div>
                <div className="shipment-delivery">Доставка: {total > 0 ? shippingNeon : null}</div>
                <div className="shipment-delivery-price">{total < 500 ? `Закажите еще на ${500 - total} ₽ для доставки за 99 ₽` : null}</div>
                <div className="shipment-coast">Итого: {total + 99} ₽</div>
            </div>
        )
    };
};