import React, { Component } from 'react';
import './app.css';
import Header from '../header/header';
import Order from '../order/order';
import MenuAdmin from '../menu-admin/menu-admin';
import sampleBurgers from '../../sample-burgers';
import Burger from '../burger/burger';
import base from '../../base';
import PropTypes from 'prop-types';
import SignIn from './../sign-in/sign-in';
import firebase from 'firebase/app';
import adminPanelSVG from './admin-panel.svg';


export default class App extends Component {

    static propTypes = {
        match: PropTypes.object
    }

    state = {
        burgers: {},
        order: {},
        display: true
    };

    componentDidMount() {
        const { params } = this.props.match;

        const localStorageRef = localStorage.getItem(params.restaurantId);
        if (localStorageRef) {
            this.setState({ order: JSON.parse(localStorageRef) });
        }

        this.ref = base.syncState(`${params.restaurantId}/burgers`, {
            context: this,
            state: 'burgers'
        });
    };

    componentDidUpdate() {
        const { params } = this.props.match;
        localStorage.setItem(params.restaurantId, JSON.stringify(this.state.order))
    };

    componentWillUnmount() {
        base.removeBinding(this.ref);
    };

    addBurger = burger => {
        const burgers = { ...this.state.burgers }; //copy state
        burgers[`burger${Date.now()}`] = burger; // add new burger in let
        this.setState({ burgers });
    };

    updateBurger = (key, updatedBurger) => {
        const burgers = { ...this.state.burgers };
        burgers[key] = updatedBurger;
        this.setState({ burgers });
    };

    deleteBurger = (key) => {
        const burgers = { ...this.state.burgers }
        burgers[key] = null;
        this.setState({ burgers });
    };

    loadSampleBurgers = () => {
        this.setState({
            burgers: sampleBurgers
        })
    };

    addToOrder = key => {
        const order = { ...this.state.order };
        order[key] = order[key] + 1 || 1;
        this.setState({ order });
    };

    deleteFromOrder = (key) => {
        const order = { ...this.state.order };
        delete order[key];
        this.setState({ order });
    };

    handleLogOut = async () => {
        await firebase.auth().signOut();
        window.location.reload();
    };

    openToAdminPanel = () => {
        const display = this.state.display;
        this.setState({ display: !display });
    };

    render() {

        const { display } = this.state;

        return (
            <SignIn>
                <div className="container">
                    <button className="btn-admin-panel" onClick={this.openToAdminPanel}>
                        <img src={adminPanelSVG} alt="admin-panel"></img>
                    </button>
                    {display ? (<div>
                        <Header title='Hot Burgers' />
                        <Order
                            burgers={this.state.burgers}
                            order={this.state.order}
                            deleteFromOrder={this.deleteFromOrder} />
                        <ul className="app-burger">
                            {Object.keys(this.state.burgers).map((key) => {
                                return <Burger
                                    key={key} W
                                    index={key}
                                    details={this.state.burgers[key]}
                                    addToOrder={this.addToOrder} />
                            })}
                        </ul>
                    </div>) : (<MenuAdmin addBurger={this.addBurger}
                        loadSampleBurgers={this.loadSampleBurgers}
                        burgers={this.state.burgers}
                        updateBurger={this.updateBurger}
                        deleteBurger={this.deleteBurger}
                        handleLogOut={this.handleLogOut} />)}
                </div>
            </SignIn>
        );
    };
};