import React, { Component } from 'react';
import './add-burger-form.css';
import PropTypes from 'prop-types';

export default class AddBurgerForm extends Component {

    static propTypes = {
        addBurger: PropTypes.func
    };

    nameRef = React.createRef()
    priceRef = React.createRef()
    statusRef = React.createRef()
    descRef = React.createRef()
    imageRef = React.createRef()

    createBurger = event => {
        event.preventDefault();
        const burger = {
            name: this.nameRef.current.value,
            price: parseFloat(this.priceRef.current.value || 0),
            status: this.statusRef.current.value,
            image: this.imageRef.current.value,
            desc: this.descRef.current.value
        };
        this.props.addBurger(burger);
        event.currentTarget.reset();
    };

    render() {
        return (
            <form className="add-burger-form" onSubmit={this.createBurger}>
                <div>
                    <input name="name" ref={this.nameRef} type="text" placeholder="Name" autoComplete="off" />
                    <input name="price" ref={this.priceRef} type="text" placeholder="Price" autoComplete="off" />
                    <select name="status" ref={this.statusRef} className="add-menu-form-status">
                        <option value="available">Доступно</option>
                        <option value="unavailable">Убрать из меню</option>
                    </select>
                    <input name="image" ref={this.imageRef} type="text" placeholder="Image" autoComplete="off" />

                </div>
                <div className="add-burger-form-textarea">
                    <textarea name="description" ref={this.descRef} placeholder="Description" />
                    <button type="submit">Добавить в меню</button>
                </div>
            </form>
        )
    }
}