import React, { Component } from 'react';
import Shipment from '../shipment/shipment';
import './order.css';
import PropTypes from 'prop-types';

export default class Order extends Component {

    static propTypes = {
        burgers: PropTypes.object,
        order: PropTypes.object,
        deleteFromOrder: PropTypes.func
    };

    renderOrder = (key) => {
        const burger = this.props.burgers[key];
        const count = this.props.order[key];

        const isAvailable = burger && burger.status === 'available';

        if(!burger) return null;

        if (!isAvailable) {
            return <li key={key}>
                Извините, {burger ? burger.name : 'бургер'} временно недоступен!
            </li>
        };
        return <li key={key}>
            <span> {count} </span>
            шт. {burger.name }
            <span>  {count * burger.price} ₽</span>
            <button onClick={()=>this.props.deleteFromOrder(key)} className="order-btn">&times;</button>
        </li>;
    };

    render() {
        const orderIds = Object.keys(this.props.order);
        const total = orderIds.reduce((prevTotal, key) => {
            const burger = this.props.burgers[key];
            const count = this.props.order[key];

            const isAvailable = burger && burger.status === 'available';
            if (isAvailable) {
                return prevTotal + burger.price * count;
            } else {
                return prevTotal;
            }
        }, 0);

        return (
            <div className="order-top">
                <div className="order-block">
                    <h2 className="order-heading">Ваш заказ:</h2>
                    <div className="order-list">
                        <ul>{orderIds.map(this.renderOrder)}</ul>
                    </div>
                </div>
                {total > 0 ? (<Shipment  total={total} />) : (<div>Выберите блюда и добавьте к заказу</div>)}
            </div>
        )
    };
};