import React from 'react';
import PropTypes from 'prop-types'; 
import './login.css';

const Login = (props) => {
    return(
        <div className="login">
            <nav className="login-nav">
                <h2>Авторизация</h2>
                <p>Введте логин и пароль вашего эккаунта Github</p>
                <button onClick={()=>props.authenticate()}>Войти</button>
            </nav>
        </div>
    )
}

Login.propTypes = {
    authenticate: PropTypes.func.isRequired
}

export default Login;