import React, { Component } from 'react';
import './menu-admin.css';
import AddBurgerForm from './../add-burger-form/add-burger-form';
import EditBurgerForm from './../edit-burger-form/edit-burger-form';
import PropTypes from 'prop-types';
import firebase from 'firebase/app';

export default class MenuAdmin extends Component {

    state = {
        photo: '',
        user: ''
    };

    static propTypes = {
        burgers: PropTypes.object,
        deleteBurger: PropTypes.func,
        updateBurger: PropTypes.func,
        addBurger: PropTypes.func,
        loadSampleBurgers: PropTypes.func,
    };

    componentDidMount() {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                this.authHandler({ user });
            }
        })
    };

    authHandler = async (authData) => {
        const { email, photoURL } = authData.user;
        this.setState({ user: email, photo: photoURL })
    };


    render() {

        const { user, photo } = this.state;
        const avatar = photo ? photo : '/images/avatar.png';

        return (
            <div className="menu-admin-top">
                {user ? <div className="menu-admin-header">
                    <div className="menu-admin-avatar">
                        <img src={avatar} alt="avatar" />
                    </div>
                    <button onClick={this.props.handleLogOut} className="menu-admin-btn-logout"> Выйти</button>
                </div> : null}
                <div className="menu-admin-block">
                    <h2 className="menu-admin-heading">Управление меню</h2>
                    {Object.keys(this.props.burgers).map((key) => {
                        return <EditBurgerForm deleteBurger={this.props.deleteBurger} index={key} updateBurger={this.props.updateBurger} key={key} burger={this.props.burgers[key]} />
                    })}
                    <div>
                        <AddBurgerForm addBurger={this.props.addBurger} />
                        <button className="menu-admin-btn" onClick={this.props.loadSampleBurgers}>Загрузить бургери</button>
                    </div>
                </div>
            </div>
        );
    };
};