import React, { Component } from 'react';
import './edit-burger-form.css';
import PropTypes from 'prop-types';

export default class EditBurgerForm extends Component {

static propTypes = {
    burger: PropTypes.shape({
        image: PropTypes.string ,
        name: PropTypes.string ,
        price: PropTypes.number ,
        desc: PropTypes.string ,
        status: PropTypes.string 
    }),
    index: PropTypes.string,
    updateBurger: PropTypes.func,
    deleteBurger: PropTypes.func,

};

    handleChange = (event) => {
        const updatedBurger = {
            ...this.props.burger,
            [event.currentTarget.name]: event.currentTarget.name === 'price' ? parseFloat(event.currentTarget.value) || 0 : event.currentTarget.value,
        };
        this.props.updateBurger(this.props.index, updatedBurger);
    };

    render() {

        const { name, image, desc, status, price } = this.props.burger;

        return (
            <div>
                <form className="edit-burger-form">
                    <div>
                        <input onChange={this.handleChange} name="name" value={name} type="text" placeholder="Name" autoComplete="off" />
                        <input onChange={this.handleChange} name="price" value={price} type="text" placeholder="Price" autoComplete="off" />
                        <select onChange={this.handleChange} name="status" value={status} className="add-menu-form-status">
                            <option value="available">Доступно</option>
                            <option value="unavailable">Убрать из меню</option>
                        </select>
                        <input onChange={this.handleChange} name="image" value={image} type="text" placeholder="Image" autoComplete="off" />

                    </div>
                    <div className="edit-burger-form-textarea">
                        <textarea onChange={this.handleChange} name="description" value={desc} placeholder="Description" />
                    </div>
                    <button onClick={()=>{this.props.deleteBurger(this.props.index)}}>Удалить из меню</button>
                </form>
            </div>
        )
    };
};